export default {
  props: {
    scrollEvent: String
  },
  mounted() {},
  destroyed() {},
  methods: {
    scroll() {
      this.$nextTick(() => {
        const container = this.$refs.scrollContainer
        if (container.children.length) {
          container.scrollTop = container.children[0].offsetHeight
        }
      })
    }
  }
}



