import Draggable from 'vuedraggable'
import { mapState } from 'vuex'
import { scan } from './InstanceScanner'

export default {
  name: 'Drag',
  data () {
    return {
      w: 360,
      h: 640,
      items: []
    }
  },
  computed: mapState({
    instances: state => state.instances
  }),
  components: {
    Draggable
  },
  watch: {
    instances (nv) {
      this.items = nv
    }
  },
  methods: {
    added(e) {
      scan(this.$refs.screen)
    }
  }
}



