import DataField from './DataField'

const keyOrder = {
  undefined: 1,
  props: 2,
  computed: 3,
  state: 1,
  getters: 2
}

export default {
  props: ['state'],
  components: {
    DataField
  },
  methods: {
    getKeys(state) {
      return ['undefined', 'props']
      // return Object.keys(state).sort((a, b) => {
      //   return (
      //     (keyOrder[a] || (a.charCodeAt(0) + 999)) -
      //     (keyOrder[b] || (b.charCodeAt(0) + 999))
      //   )
      // })
    },
    toDisplayType(type, asClass) {
      return type === 'undefined' ?
        'data' :
        asClass ?
        type.replace(/\s/g, '-') :
        type
    }
  }
}



