import QR from 'vue-qrcode-component'
// import Bar from 'vue-barcode'
import _ from 'lodash'

export default {
  name: 'QRCode',
  components: {
    // Bar,
    QR
  },
  data() {
    return {
      qrs: ['15618684998', '123456', ''],
      b: ''
    }
  },
  methods: {
    addBlk() {
      this.qrs.push('')
    },
    batch() {
      this.qrs = _.concat(this.qrs, _.split(this.b, ','))
      this.b = ''
    }
  }
}



