import Draggable from 'vuedraggable'
import { buttonGroup, dropdown, radio, aside as sidebar, accordion, alert, datepicker, select as vSelect, option as vOption } from 'vue-strap'
import hub from '@/utils/eventhub'
import _ from 'lodash'
import CompDropdown from '@/comps/CompDropdown'
import Vue from 'vue'
export default {
  name: 'Drag',
  data () {
    return {
      //组件集合
      comps: [
        {name: 'Accordion', comp: accordion},
        {name: 'Alert', comp: alert},
        {name: 'Datepicker', comp: datepicker},
        {name: 'CompDropdown', comp: CompDropdown},
        {name: 'Dropdown', comp: dropdown}
      ],
      // 元素集合(拖拽产物)
      items: [],
      // 当前编辑节点
      currItem: '',
      p: {size: 'lg', text: 'testsss'},
      meta: 'dropdown'
    }
  },
  computed: {
    props () {
      if (!this.currItem) {
        return []
      }
      return this.currItem.props()
    }
  },
  methods: {
    changeCurr (idx) {
      this.currItem = this.$refs.items[idx]
    }
  },
  watch: {
    items (nv) {
      // console.log(nv)
      // let page = this.$refs.items
      // console.log(page)
      // console.log(page.children.size())
      _.forEach(nv, c => {
        console.log(c.comp.data)
        if(typeof(c) === 'VueComponent'){
        }
      })
      // hub.$emit('edit.curr_page', this.$refs.appPage)
    },
    currItem (nv, ov) {
      console.log(nv)
      nv && nv.$el.classList.toggle('curr')
      ov && ov.$el.classList.toggle('curr')
    }
  },
  components: {
    radio,
    dropdown,
    buttonGroup,
    Draggable,
    sidebar, accordion, alert, datepicker,CompDropdown, vSelect, vOption,
  },
  created () {
    _.forEach(this.comps, (v) => { if(!v.comp.name) {v.comp.name = v.name} } )
  },
  mounted () {
    hub.$emit('aside')
  },
  beforeDestroy () {
    hub.$emit('aside')
  },
  updated () {
    // hub.$emit('edit.curr_page', this.$refs.appPage)
  }
}




